import { Component, OnInit } from '@angular/core';
import { FlatpickrOptions } from 'ng2-flatpickr';
import { Location } from '@angular/common';
import { PaginationService } from 'src/app/service/pagination.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { CommonFunction } from 'src/app/common';
import { ToastrService } from 'ngx-toastr';
import { ActionField, TextField, CheckBoxFildes } from 'src/app/common-module/table-list/list-field-type';
import { PickListService } from 'src/app/service/pick-list.service';
import { DataService } from 'src/app/service/data.service';
import { DATEFORMAT } from 'src/app/messages';
declare var $: any;
@Component({
  selector: 'app-generate',
  templateUrl: './generate.component.html'
})
export class GenerateComponent implements OnInit {

  PageTitle = "Genrate PickList";
  objectDetails: any = false;
  productList: any = [];
  productItems: Array<any> = [];
  headerData: Array<any> = [];
  pagination: any;
  searchText: any;
  currentPage: any;
  orderId: any;

  currentCompany:any;
  date_format:string="M/d/yy";
  timezone:string
  time_format:string;

  addForm: FormGroup;
  showLoader = false;
  formErrors = {
    apierror: null,
  };
  isEditing: any;
  editId: any;


  constructor(
    private location: Location,
    private toastr: ToastrService,
    private picklistService: PickListService,
    private paginationService: PaginationService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private dataService:DataService,
    private router: Router,
  ) {




    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      let id = this.activatedRoute.snapshot.paramMap.get('id');
      if (location.path() == `/outbound/pick-list/generate/${id}`) {
        this.orderId = this.activatedRoute.snapshot.paramMap.get('id');
        if (this.orderId) {
          this.getUnloadGenerateListById();
        }
      } else {
        this.editId = id;
        this.isEditing = true;
      }
    }

    this.addForm = this.fb.group({
      pick_list_date: [null, Validators.compose([Validators.required])],
      sales_order_date: [null, Validators.compose([Validators.required])],
    });
  }

  exampleOptions: FlatpickrOptions = {
    defaultDate: new Date(),
    dateFormat	:DATEFORMAT
    //enableTime: true
  };

  ngOnInit() {
    this.dataService.currentCompany.subscribe((response)=>{
      if(response){
      this.timezone=String(response.timezone.format);;
      this.time_format=response.time_format.label;
      this.date_format=response.date_format.label;
      this.currentCompany=response;
      }
      
    });
    this.addForm.patchValue({ pick_list_date: new Date() });
    this.setHeaderData();
  }

  setHeaderData() {
    this.headerData.push(

      new CheckBoxFildes({
        class: 'align-items-center d-flex',
        key: "sales_order_detail_id",
      }),
      new TextField({
        label: "SKU",
        key: "sku_no",
      }),
      new TextField({
        label: "Product",
        key: "product_name",
      }),
      new TextField({
        label: "Order Quantity",
        key: "qty",
      })
    );
  }

  back() {
    this.location.back();
    // this.router.navigateByUrl(`/inbound/order/view/${this.orderId}/unloading`);
  }

  getUnloadGenerateListById() {
    this.picklistService.getPickListGenerateListById(this.orderId).subscribe((response) => {
      if (response.success) {
        this.objectDetails = response.data.sales_order_detail ? response.data.sales_order_detail : {};
        this.productList = response.data.product_list ? response.data.product_list : [];
        this.setData();
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectDetails = null;
        this.productList = [];
        this.pagination = null;
      }
    });
  }


  setData() {
    this.searchText = this.objectDetails.sales_order_no;
    this.addForm.patchValue({
      sales_order_date: this.objectDetails.sales_order_date,
      pick_list_date: new Date()
    });
  }

  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('pick_list_date', CommonFunction.changedateFormate(formData.value.pick_list_date[0]));
      data.append('po_id', this.orderId);
      // data.append('supplier_id', this.objectDetails.supplier_id);
      data.append('client_id', this.objectDetails.client_id);
      data.append('product', JSON.stringify(this.productItems));
      data.append('sales_order_id', JSON.stringify(this.orderId));
      data.append('pick_list_no', JSON.stringify(this.objectDetails.sales_order_no));

      if (this.isEditing) {
        // this.picklistService.editUnload(this.editId, data).subscribe((response) => {
        //   this.showLoader = false;
        //   if (response.success) {
        //     this.router.navigateByUrl('/inbound/unload/');
        //   }
        // }, (error) => {
        //   this.showLoader = false;
        // });
      } else {
        this.picklistService.addPickListGenerate(data).subscribe((response) => {
          setTimeout(() => {
            this.showLoader = false;
          }, 700);
          if (response.success) {
            // this.router.navigateByUrl(`/inbound/order/view/${this.orderId}`);
            this.back();
          } else {
            response.error.filter((data) => {
              if (data.hasOwnProperty('product')) {
                this.toastr.error('', 'Please Select a Product...!');
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }

  getPage(page: number) {
    this.currentPage = page;

  }

  searchObject() {
    console.log("searchObject", this.searchText);
  }

  onReloadEvent() {
    this.getUnloadGenerateListById();
  }

  checkBoxSelectEvent(data) {
    this.productItems = [];
    if (data && data.length > 0) {
      data.filter((obj) => {
        let object = new Object();
        object['product_id'] = obj.product_id;
        object['product_qty'] = obj.qty;
        this.productItems.push(object);
      });
    }
  }

}
