import { Component, OnInit } from '@angular/core';
import { FlatpickrOptions } from 'ng2-flatpickr';
import { Location } from '@angular/common';
import { UnloadService } from 'src/app/service/unload.service';
import { PaginationService } from 'src/app/service/pagination.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { CommonFunction } from 'src/app/common';
import { ToastrService } from 'ngx-toastr';
import { ActionField, TextField, CheckBoxFildes } from 'src/app/common-module/table-list/list-field-type';
import { DataService } from 'src/app/service/data.service';
import { DATEFORMAT } from 'src/app/messages';
declare var $: any;
@Component({
  selector: 'app-generate',
  templateUrl: './generate.component.html'
})
export class GenerateComponent implements OnInit {

  PageTitle = "Genrate Unloading";
  objectDetails: any = false;
  productList: any = [];
  productItems: Array<any> = [];
  headerData: Array<any> = [];
  pagination: any;
  searchText: any;
  currentPage: any;
  orderId: any;
  currentCompany:any;
  date_format:string="M/d/yy";
  timezone:string
  time_format:string;

  addForm: FormGroup;
  showLoader = false;
  formErrors = {
    apierror: null,
  };
  isEditing: any;
  editId: any;


  constructor(
    private location: Location,
    private toastr: ToastrService,
    private unloadService: UnloadService,
    private paginationService: PaginationService,
    private activatedRoute: ActivatedRoute,
    private dataService:DataService,
    private fb: FormBuilder,
    private router: Router,
  ) {

    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.orderId = this.activatedRoute.snapshot.paramMap.get('id');
      if (this.orderId) {
        this.getUnloadGenerateListById();
      }
    }

    this.addForm = this.fb.group({
      receipt_date: [null, Validators.compose([Validators.required])],
      po_date: [null, Validators.compose([Validators.required])],
    });
  }

  exampleOptions: FlatpickrOptions = {
    defaultDate: new Date(),
    dateFormat	:DATEFORMAT
    //enableTime: true
  };

  ngOnInit() {
    this.dataService.currentCompany.subscribe((response)=>{
      if(response){
      this.timezone=String(response.timezone.format);;
      this.time_format=response.time_format.label;
      this.date_format=response.date_format.label;
      this.currentCompany=response;
      }
      
    });
    this.addForm.patchValue({ receipt_date: new Date() });
    this.setHeaderData();
  }

  setHeaderData() {
    this.headerData.push(

      new CheckBoxFildes({
        class: 'align-items-center d-flex',
        key: "product_id",
      }),

      new TextField({
        label: "SKU",
        key: "sku_no",
      }),
      new TextField({
        label: "Product",
        key: "product_name",
      }),
      new TextField({
        label: "Order Quantity",
        key: "order_quantity",
      }) ,



    );
  }

  back() {
    this.location.back();
    // this.router.navigateByUrl(`/inbound/order/view/${this.orderId}/unloading`);
  }

  getUnloadGenerateListById() {
    this.unloadService.getUnloadGenerateListById(this.orderId).subscribe((response) => {
      if (response.success) {
        this.objectDetails = response.data.po_detail ? response.data.po_detail : {};
        this.productList = response.data.product_list ? response.data.product_list : [];
        this.setData();
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectDetails = null;
        this.productList = [];
        this.pagination = null;
      }
    });
  }


  setData() {
    this.searchText = this.objectDetails.po_no;
    this.addForm.patchValue({
      po_date: this.objectDetails.po_date,
      receipt_date: new Date()
    });
  }

  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('receipt_date', CommonFunction.changedateFormate(formData.value.receipt_date[0]));
      data.append('po_id', this.orderId);
      data.append('supplier_id', this.objectDetails.supplier_id);
      data.append('to_details', this.objectDetails.client_id);
      data.append('product', JSON.stringify(this.productItems));
      if (this.isEditing) {
        // this.unloadService.editUnload(this.editId, data).subscribe((response) => {
        //   this.showLoader = false;
        //   if (response.success) {
        //     this.router.navigateByUrl('/inbound/unload/');
        //   }
        // }, (error) => {
        //   this.showLoader = false;
        // });
      } else {
        this.unloadService.addUnloadGenerate(data).subscribe((response) => {
          setTimeout(() => {
            this.showLoader = false;
          }, 700);
          if (response.success) {
            // this.router.navigateByUrl(`/inbound/order/view/${this.orderId}`);
            this.back();
          } else {
            response.error.filter((data) => {
              if (data.hasOwnProperty('product')) {
                this.toastr.error('', 'Please Select a Product...!');
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }

  // markAllProduct(checked) {
  //   if (checked) {
  //     this.productItems = this.productList.map((obj, index) => {
  //       const object = new Object();
  //       object['product_id'] = obj.product_id;
  //       object['order_qty'] = obj.order_quantity;
  //       object['sku_no'] = obj.sku_no;
  //       object['product_name'] = obj.product_name;
  //       $('.pro-chk').prop('checked', true);
  //       return object;
  //     });
  //   } else {
  //     $('.pro-chk').prop('checked', false);
  //     this.productItems = [];
  //   }
  // }

  // addProduct(obj, checked) {
  //   if (checked) {
  //     const object = new Object();
  //     object['product_id'] = obj.product_id;
  //     object['order_qty'] = obj.order_quantity;
  //     object['sku_no'] = obj.sku_no;
  //     object['product_name'] = obj.product_name;
  //     this.productItems.push(object);
  //     if (this.productItems.length === this.productItems.length) {
  //       $('#checkBoxAll').prop('checked', true);
  //     }
  //   } else {
  //     $('#checkBoxAll').prop('checked', false);
  //     this.productItems = this.productItems.filter((object) => {
  //       return String(object.product_id) !== String(obj.product_id);
  //     });
  //   }
  // }

  getPage(page: number) {
    this.currentPage = page;

  }

  searchObject() {
    console.log("searchObject", this.searchText);
  }

  onReloadEvent() {
    this.getUnloadGenerateListById();
  }

  checkBoxSelectEvent(data) {
    this.productItems = [];
    if (data && data.length > 0) {
      data.filter((obj) => {
        let object = new Object();
        object['product_id'] = obj.product_id;
        object['order_qty'] = obj.order_quantity;
        object['sku_no'] = obj.sku_no;
        object['product_name'] = obj.product_name;
        this.productItems.push(object);
      });
    }
  }

}
